// Main SCSS file
@import "_resets";

$primary-color: #479AA1;
$secondary-color: #F5AEA8;
$accent-color: #D9D9D9;
$dark-color: #707070;
$light-color: #FFF;

$header-font-family: 'Raleway', sans-serif;;
$paragraph-font-family: 'Nunito', sans-serif;;

$light_font: 200;
$regular_font: 400;
$bold_font: 600;
$extra_bold_font: 700;

$cta-button-font-size: 1rem;
$learnmore-button-font-size: .7rem;
$button-border-radius: 5px;

h1, h2, h3, h4 {font-family: $header-font-family;}

p {font-family: $paragraph-font-family;}

@import "_utility";

@import "_navbar";
@import "_buttons";
@import "_header";
@import "_intro-section";
@import "_triple-features";
@import "_upcoming-retreat";
